	export default {
		data() {
			return {
				payType: 1,
				uid:0,
				moneyAmount:0,
				totalMoney:0,
				orderInfo: {},
				goodNames:"",
				walletId:0,
				orderId:0
			};
		},
		computed: {
		
		},
		onLoad(options) {
			this.totalMoney = options.totalMoney;
			this.uid = options.uid;
			this.goodNames = options.goodNames;
			this.orderId = options.orderId;
			var _this = this;
			uni.request({
				url: this.apiServer+'/customer/getById',
				dataType: "json",
				data: {
					"uid": _this.uid
				},
				success: (res) => {
					const result = res.data;
                     _this.moneyAmount = result.moneyAmount;
					 _this.walletId = result.walletId;
					
			    }
			});
		},

		methods: {
			//选择支付方式
			changePayType(type) {
				this.payType = type;
			},
			//确认支付
			confirm: async function() {
				if(this.payType == 3){
					if(this.totalMoney > this.moneyAmount){
						this.$api.msg('余额不足');
						return;
					}
					var params = {
						"name":this.goodNames,
						"consumePrice":this.totalMoney,
						"customerId":this.uid,
						"walletId":this.walletId,
						"orderId":this.orderId
					};
					uni.request({
					    url: this.apiServer + "/api/pay/payBill",
							 method:"POST",
							 data:params,
							 dataType: "json",
							 success: function(res) {
							   const result = res.data;
							   if(result){
								  uni.redirectTo({
								  	url: '/pages/money/paySuccess'
								  }) 
							   }else{
								   	_this.$api.msg("支付失败");
							   }
							 },
						});
				}

			},
		}
	}
